import { useState, useEffect } from "react"
import { Box, Typography } from "@mui/material"
import DefaultInvestmentCard from "../../examples/Cards/ProjectCards/DefaultInvestmentCard"

const Countdown = props => {
  const [disabledCard, setDisabledCard] = useState(false)
  const [bottomPosition, setBottomPosition] = useState("140px")
  const [fontSizeNumbers, setFontSizeNumbers] = useState("35px")
  const [fontSizeLetters, setFontSizeLetters] = useState("20px")

  useEffect(() => {
    const updateBottomPosition = () => {
      if (window.innerWidth <= 380 && window.innerWidth >= 346) {
        setBottomPosition("125px")
        setFontSizeLetters("19px")
      } else if (window.innerWidth <= 345 && window.innerWidth >= 326) {
        setBottomPosition("115px")
        setFontSizeNumbers("32px")
        setFontSizeLetters("18px")
      } else if (window.innerWidth <= 325 && window.innerWidth >= 306) {
        setBottomPosition("105px")
        setFontSizeNumbers("30px")
        setFontSizeLetters("17px")
      } else if (window.innerWidth <= 305 && window.innerWidth >= 286) {
        setBottomPosition("95px")
        setFontSizeNumbers("28px")
        setFontSizeLetters("16px")
      } else if (window.innerWidth <= 285 && window.innerWidth >= 276) {
        setBottomPosition("85px")
        setFontSizeNumbers("26px")
        setFontSizeLetters("15px")
      } else if (window.innerWidth <= 275 && window.innerWidth >= 256) {
        setBottomPosition("75px")
        setFontSizeNumbers("24px")
        setFontSizeLetters("14px")
      } else if (window.innerWidth <= 255 && window.innerWidth >= 236) {
        setBottomPosition("65px")
        setFontSizeNumbers("23px")
        setFontSizeLetters("13px")
      } else if (window.innerWidth <= 235 && window.innerWidth >= 221) {
        setBottomPosition("55px")
        setFontSizeNumbers("22px")
        setFontSizeLetters("12px")
      } else if (window.innerWidth <= 220 && window.innerWidth >= 201) {
        setBottomPosition("45px")
        setFontSizeNumbers("21px")
        setFontSizeLetters("11px")
      } else if (window.innerWidth <= 200) {
        setBottomPosition("35px")
        setFontSizeNumbers("20px")
        setFontSizeLetters("10px")
      } else {
        setBottomPosition("140px")
        setFontSizeNumbers("35px")
        setFontSizeLetters("20px")
      }
    }
    updateBottomPosition()
    window.addEventListener("resize", updateBottomPosition)
    return () => {
      window.removeEventListener("resize", updateBottomPosition)
    }
  }, [])
  const lastHouse = props.house

  const classes = {}
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0
  })

  useEffect(() => {
    const interval = setInterval(() => {
      const currentDate = new Date()
      const distance = props.endDate - currentDate
      const days = Math.floor(distance / (1000 * 60 * 60 * 24))
      const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
      const seconds = Math.floor((distance % (1000 * 60)) / 1000)
      setTimeLeft({ days, hours, minutes, seconds })
      console.log("Seconds", seconds)
      if (days >= 0 && hours >= 0 && minutes >= 0 && seconds > 0) {
        setDisabledCard(true)
        console.log("Disableed", disabledCard)
      } else if (days === 0 && hours === 0 && minutes === 0 && seconds === 0) {
        setDisabledCard(false)
        console.log("disabled = false", setDisabledCard(false))
      }
      if (distance < 0) {
        clearInterval(interval)
        setTimeLeft({
          days: 0,
          hours: 0,
          minutes: 0,
          seconds: 0
        })
        return
      }
    }, 1000)
    return () => {
      clearInterval(interval)
      console.log("Interval", interval)
      console.log("Clear Interval", clearInterval(interval))
    }
  }, [props.endDate, disabledCard])

  const handleRocket = () => {
    console.log(props.houses)
    console.log(props.endDate)
    setDisabledCard(!disabledCard)
    console.log("Disableed", !disabledCard)

    console.log("Disableed", disabledCard)
  }

  return (
    <Box sx={{ position: "relative", display: "flex", alignItems: "center", justifyContent: "center" }}>
      {disabledCard && (
        <Box
          sx={{
            display: "flex",
            flexWrap: "wrap",
            alignItems: "center",
            justifyContent: "center",
            position: "absolute",
            width: "100%",
            height: "180px",
            bottom: bottomPosition,
            zIndex: 999
          }}>
          <Box sx={{ pb: 10, display: "flex", flexWrap: "wrap", gap: "25px", alignItems: "center", justifyContent: "center" }}>
            <Box className={classes.item} sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
              <Typography className={classes.number} sx={{ fontWeight: "bold", fontSize: fontSizeNumbers, color: "#1970e3" }}>
                {timeLeft.days}
              </Typography>
              <Typography className={classes.unit} sx={{ fontSize: fontSizeLetters, fontWeight: 550, color: "#fff", textShadow: "black 0.6px 0 0.5px" }}>
                Days
              </Typography>
            </Box>
            <Box className={classes.item} sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
              <Typography className={classes.number} sx={{ fontWeight: "bold", fontSize: fontSizeNumbers, color: "#1970e3" }}>
                {timeLeft.hours}
              </Typography>
              <Typography className={classes.unit} sx={{ fontSize: fontSizeLetters, fontWeight: 550, color: "#fff", textShadow: "black 0.6px 0 0.5px" }}>
                Hours
              </Typography>
            </Box>
            <Box className={classes.item} sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
              <Typography className={classes.number} sx={{ fontWeight: "bold", fontSize: fontSizeNumbers, color: "#1970e3" }}>
                {timeLeft.minutes}
              </Typography>
              <Typography className={classes.unit} sx={{ fontSize: fontSizeLetters, fontWeight: 550, color: "#fff", textShadow: "black 0.6px 0 0.5px" }}>
                Minutes
              </Typography>
            </Box>
            <Box className={classes.item} sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
              <Typography className={classes.number} sx={{ fontWeight: "bold", fontSize: fontSizeNumbers, color: "#1970e3" }}>
                {timeLeft.seconds}
              </Typography>
              <Typography className={classes.unit} sx={{ fontSize: fontSizeLetters, fontWeight: 550, color: "#fff", textShadow: "black 0.6px 0 0.5px" }}>
                Seconds
              </Typography>
            </Box>
          </Box>
        </Box>
      )}
      <Box
        sx={{
          pb: 3.5,
          opacity: disabledCard ? 0.6 : 1,
          filter: disabledCard ? "blur(4px)" : "blur(0px)",
          WebkitBackdropFilter: disabledCard ? "blur(4px)" : "blur(0px)"
        }}>
        {lastHouse ? (
          <DefaultInvestmentCard
            id={lastHouse.house_id}
            image={lastHouse.pic}
            label={lastHouse.location}
            title={lastHouse.title}
            networkProgress={lastHouse.total_investments - lastHouse.personal_invested_amount}
            personalProgress={lastHouse.personal_invested_amount}
            missingProgress={lastHouse.total_required - lastHouse.total_investments}
            value={lastHouse.price}
            caption="per month"
            duration={lastHouse.duration}
            priority="auto"
            loading="lazy"
            isDisabled={disabledCard}
          />
        ) : null}
      </Box>
    </Box>
  )
}

export default Countdown
