import MDBox from "../../../components/MDBox"
import MDTypography from "../../../components/MDTypography"
import { Dialog, DialogActions, Divider, Grid, Slide, TextField, Typography } from "@mui/material"
import DefaultInvestmentCard from "../../../examples/Cards/ProjectCards/DefaultInvestmentCard"
import { forwardRef, useState } from "react"
import SearchIcon from "../../../icons/SearchIcon"
import tuneIcon from "../../../icons/tuneIcon"
import Calendar from "react-calendar"
import MDButton from "../../../components/MDButton"
import Countdown from "../countdown"

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})

function MarketPlace(props) {
  const [isOpen, setIsOpen] = useState(false)
  const [open, setOpen] = useState(false)
  const [searchText, setSearchText] = useState("")
  const [selectedDateRange, setSelectedDateRange] = useState(null)

  const handleClickOpen = () => {
    return
    setOpen(true)
  }
  const handleOpenSearch = () => {
    return
    setIsOpen(!isOpen)
  }
  const handleClose = () => {
    setOpen(false)
  }

  const handleSearch = () => {
    const filteredData = props?.filter(props => {
      const includesSearchText =
        props?.title?.toLowerCase().includes(searchText?.toLowerCase()) || props?.message?.toLowerCase().includes(searchText?.toLowerCase())

      const isInRange =
        selectedDateRange === null || (props?.date && new Date(props.date) >= selectedDateRange[0] && new Date(props.date) <= selectedDateRange[1])

      return includesSearchText && isInRange
    })

    props = filteredData

    handleClose()
  }

  return (
    <>
      <MDBox display="flex" justifyContent="space-between" alignItems="center" pt={2.3} pb={0.4} px={2}>
        <MDTypography variant="h5" sx={{ fontSize: "18px" }}>
          Listed Properties
        </MDTypography>
        <MDBox sx={{ display: "flex", alignItems: "baseline", cursor: "pointer", justifyContent: "center", gap: "5px" }}>
          <SearchIcon onClick={handleOpenSearch} />
          <button
            onClick={handleClickOpen}
            style={{
              border: "none",
              backgroundColor: "transparent",
              cursor: "pointer",
              fontSize: "20px",
              color: "rgb(52, 71, 103)"
            }}>
            {tuneIcon}
          </button>
        </MDBox>
      </MDBox>
      {isOpen ? (
        <MDBox sx={{ px: "12px", pt: "15px" }}>
          <TextField
            color="warning"
            sx={{ width: "100%", minWidth: "80px" }}
            id="outlined-basic"
            label="Search..."
            variant="outlined"
            value={searchText}
            onChange={e => setSearchText(e.target.value)}
            onKeyPress={e => {
              if (e.key === "Enter") {
                handleSearch()
              }
            }}
          />
        </MDBox>
      ) : null}
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
        maxWidth="sm"
        fullWidth>
        <Calendar selectRange onChange={newDateRange => setSelectedDateRange(newDateRange)} />
        <DialogActions>
          <MDButton onClick={handleClose}>Cancel</MDButton>
          <MDButton color="info" variant="gradient" onClick={handleSearch}>
            Filter
          </MDButton>
        </DialogActions>
      </Dialog>
      <Divider />
      <MDBox>
        <Grid
          sx={{
            px: 2
          }}
          container
          spacing={3.7}>
          {Array.isArray(props.houses) &&
            props?.houses.map((item, index) => (
              <Grid item xs={12} md={6} xl={3} key={item.house_id} data-test-id={`market-property-${index}`}>
                {item.comingsoon ? (
                  <Countdown house={item} endDate={new Date("Sep 25, 2023 17:23:00")} />
                ) : (
                  <DefaultInvestmentCard
                    id={item.house_id}
                    image={item.pic}
                    label={item.location}
                    title={item.title}
                    networkProgress={item.total_investments - item.personal_invested_amount}
                    personalProgress={item.personal_invested_amount}
                    missingProgress={item.total_required - item.total_investments}
                    value={item.price}
                    caption="per month"
                    duration={item.duration}
                    priority={index < 5 ? "high" : "auto"}
                    loading={index < 5 ? "eager" : "lazy"}
                  />
                )}
              </Grid>
            ))}
        </Grid>
        {props.houses?.length === 0 && (
          <Typography
            variant="body1"
            sx={{
              fontSize: "1rem",
              lineHeight: 1.375,
              fontWeight: 400,
              letterSpacing: "0em",
              paddingLeft: "15px",
              paddingTop: "28px",
              opacity: 1,
              textTransform: "none",
              verticalAlign: "unset",
              color: "#7b809a",
              margin: 0,
              fontFamily: '"Roboto", "Helvetica", sansSerif',
              display: "flex",
              alignItems: "center",
              pb: 2.2
            }}>
            There are no listed properties yet!
          </Typography>
        )}
      </MDBox>
    </>
  )
}

export default MarketPlace
